import React, { useContext, useState } from 'react';
import './Contact.scss';
import DarkModeContext from './../context/DarkModeContext';
import Modal from 'react-modal';

Modal.setAppElement('#root'); // Set this according to your app's root element

const Contact = () => {
  const { isDarkMode } = useContext(DarkModeContext);
  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [externalLink, setExternalLink] = useState('');

  const openModal = (url) => {
    setExternalLink(url);
    setModalIsOpen(true);
  };

  const closeModal = () => {
    setModalIsOpen(false);
  };

  const handleExternalLinkClick = (url) => {
    window.open(url, '_blank', 'noopener,noreferrer');
    closeModal();
  };

  return (
    <section id="contact" className={`contact-section ${isDarkMode ? 'dark-mode' : ''}`}>
      <div className="container">
        <h2 className="mb-5 maven-font heading text-center">Contact</h2>
        <div className="row">
          <div className="col-sm-12">
            <div id="social-media">
              <a 
                href="https://in.linkedin.com/in/sangameshmyageri" 
                onClick={(e) => {
                  e.preventDefault();
                  openModal('https://in.linkedin.com/in/sangameshmyageri');
                }}
                title="Sangamesh Myageri Linkedin"
                aria-label="Sangamesh Myageri Linkedin"
              >
                <i className="fab fa-linkedin"></i>
              </a>
              <a 
                href="https://x.com/sangumyageri"
                onClick={(e) => {
                  e.preventDefault();
                  openModal('https://x.com/sangumyageri');
                }}
                title="Sangamesh Myageri Twitter"
                aria-label="Sangamesh Myageri Twitter"
              >
                <i className="fab fa-twitter"></i>
              </a>
              <a 
                href="https://www.instagram.com/sangu.myageri"
                onClick={(e) => {
                  e.preventDefault();
                  openModal('https://www.instagram.com/sangu.myageri');
                }}
                title="Sangamesh Myageri Instagram"
                aria-label="Sangamesh Myageri Instagram"
              >
                <i className="fab fa-instagram"></i>
              </a>
            </div>
          </div>
        </div>
        <Modal
          isOpen={modalIsOpen}
          onRequestClose={closeModal}
          contentLabel="External Link Confirmation"
          className={`Modal ${isDarkMode ? 'dark-mode' : ''}`}
          overlayClassName="Overlay"
        >
          <h2>Leave site?</h2>
          <p>You are about to visit an external site. Do you want to proceed?</p>
          <button onClick={closeModal} className="btn btn-secondary">Cancel</button>
          <button onClick={() => handleExternalLinkClick(externalLink)} className="btn btn-primary">Proceed</button>
        </Modal>
      </div>
    </section>
  );
};

export default Contact;
