import React, { useContext } from 'react';
import './Skills.scss';
import DarkModeContext from './../context/DarkModeContext';

const skills = [
    { name: 'HTML5', icon: 'fab fa-html5' },
    { name: 'CSS3', icon: 'fab fa-css3-alt' },
    { name: 'JavaScript', icon: 'fab fa-js' },
    { name: 'Drupal', icon: 'fab fa-drupal' },
    { name: 'PHP', icon: 'fab fa-php' },
    { name: 'React', icon: 'fab fa-react' },
    { name: 'TypeScript', icon: 'fas fa-code' },
    { name: 'jQuery', icon: 'fab fa-js-square' },
    { name: 'Git', icon: 'fab fa-git-alt' },
    { name: 'VS Code', icon: 'fas fa-code' },
    { name: 'Bitbucket', icon: 'fab fa-bitbucket' },
    { name: 'Jira', icon: 'fab fa-jira' },
];

const Skills = () => {
    const { isDarkMode } = useContext(DarkModeContext);

    return (
        <section id="skills" className={`skills-section ${isDarkMode ? 'dark-mode' : ''}`}>
            <div className="container">
                <h2 className="mb-5 maven-font heading">Skills</h2>
                <div className="row">
                    {skills.map((skill, index) => (
                        <div key={index} className="col-6 col-sm-4 col-md-3 col-lg-2 col-xl-2 skill-item">
                            <i className={skill.icon} aria-hidden="true"></i>
                            <p>{skill.name}</p>
                        </div>
                    ))}
                </div>
            </div>
        </section>
    );
};

export default Skills;
