import React, { useContext, useEffect, useState } from 'react';
import DarkModeContext from '../context/DarkModeContext';
import './../components/MobileNavigation.css';

const sections = ['home', 'about', 'experience', 'projects', 'contact'];

const MobileNavigation = () => {
  const { isDarkMode } = useContext(DarkModeContext);
  const [activeSection, setActiveSection] = useState('home');

  useEffect(() => {
    const handleScroll = () => {
      const scrollPosition = window.scrollY + window.innerHeight / 2;
      sections.forEach(section => {
        const element = document.getElementById(section);
        if (element) {
          const elementTop = element.offsetTop;
          const elementHeight = element.offsetHeight;
          if (scrollPosition >= elementTop && scrollPosition < elementTop + elementHeight) {
            setActiveSection(section);
          }
        }
      });
    };

    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  const handleClick = (section) => {
    setActiveSection(section);
    const element = document.getElementById(section);
    if (element) {
      element.scrollIntoView({ behavior: 'smooth' });
    }
  };

  return (
    <nav className={`navbar fixed-bottom navbar-light bg-custom d-md-none d-lg-none ${isDarkMode ? 'dark-mode' : ''}`}>
      <div className="container-fluid">
        {sections.map((section) => (
          <a
            key={section}
            className={`nav-link ${activeSection === section ? 'active' : ''}`}
            href={`#${section}`}
            onClick={() => handleClick(section)}
            title={section.charAt(0).toUpperCase() + section.slice(1)}
          >
            <i className={`fas fa-${section === 'home' ? 'home' : section === 'about' ? 'user' : section === 'experience' ? 'briefcase' : section === 'projects' ? 'project-diagram' : 'envelope'}`}></i>
            <span>{section.charAt(0).toUpperCase() + section.slice(1)}</span>
          </a>
        ))}
      </div>
    </nav>
  );
};

export default MobileNavigation;
