import React, { useState, useContext } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Autoplay, Pagination, Navigation } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/autoplay';
import './Projects.scss';
import superempackers from './../images/superem-packers-movers.png';
import opendata from './../images/opendata-portal.png';
import Modal from 'react-modal';
import DarkModeContext from './../context/DarkModeContext';

Modal.setAppElement('#root');

const projects = [
  {
    href: "https://www.suprememoversandpacker.com/",
    imgSrc: superempackers,
    imgAlt: "Supreme Movers and Packer",
    imgTitle: "Supreme Movers and Packer",
    title: "Supreme Movers and Packer",
    description: "Packer and Movers Website"
  },
  {
    href: "https://opendata.cscl.co.in/",
    imgSrc: opendata,
    imgAlt: "Open Data Portal",
    imgTitle: "Open Data Portal",
    title: "Open Data Portal",
    description: "Portal to access the open data collections"
  },
  {
    href: "https://www.suprememoversandpacker.com/",
    imgSrc: superempackers,
    imgAlt: "Supreme Movers and Packer",
    imgTitle: "Supreme Movers and Packer",
    title: "Supreme Movers and Packer",
    description: "Packer and Movers Website"
  },
  {
    href: "https://opendata.cscl.co.in/",
    imgSrc: opendata,
    imgAlt: "Open Data Portal",
    imgTitle: "Open Data Portal",
    title: "Open Data Portal",
    description: "Portal to access the open data collections"
  },
];

const Projects = () => {
  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [externalLink, setExternalLink] = useState('');
  const { isDarkMode } = useContext(DarkModeContext);

  const openModal = (url) => {
    setExternalLink(url);
    setModalIsOpen(true);
  };

  const closeModal = () => {
    setModalIsOpen(false);
  };

  const handleExternalLinkClick = () => {
    window.open(externalLink, '_blank', 'noopener,noreferrer');
    closeModal();
  };

  return (
    <section id="projects" className={`projects-section ${isDarkMode ? 'dark-mode' : ''}`}>
      <div className="container">
        <h2 className="mb-5 maven-font heading text-center">Projects</h2>
        <Swiper
          modules={[Autoplay, Navigation, Pagination]}
          spaceBetween={30}
          loop={true}
          slidesPerView={1}
          centeredSlides={true}
          autoplay={{
            delay: 2000,
            disableOnInteraction: false,
          }}
          pagination={{ clickable: true }}
          navigation
          breakpoints={{
            768: {
              slidesPerView: 2,
            },
            1024: {
              slidesPerView: 3,
            },
          }}
        >
          {projects.map((project, index) => (
            <SwiperSlide key={index}>
              <a 
                href={project.href} 
                onClick={(e) => {
                  e.preventDefault();
                  openModal(project.href);
                }}
                title={project.imgTitle}
              >
                <div className="card project-card">
                  <div className="card-img-container">
                    <img
                      src={project.imgSrc}
                      className="card-img-top project-img"
                      alt={project.imgAlt}
                      title={project.imgTitle}
                      loading="lazy"
                    />
                  </div>
                  <div className="card-body">
                    <h3 className="card-title">{project.title}</h3>
                    <p className="card-text">{project.description}</p>
                  </div>
                </div>
              </a>
            </SwiperSlide>
          ))}
        </Swiper>

        <Modal
          isOpen={modalIsOpen}
          onRequestClose={closeModal}
          contentLabel="External Link Confirmation"
          className={`Modal ${isDarkMode ? 'dark-mode' : ''}`}
          overlayClassName="Overlay"
        >
          <h2>Confirm Navigation</h2>
          <p>You are about to visit an external site. Do you want to proceed?</p>
          <button onClick={closeModal} className="btn btn-secondary">Cancel</button>
          <button onClick={handleExternalLinkClick} className="btn btn-primary">Proceed</button>
        </Modal>
      </div>
    </section>
  );
};

export default Projects;
