import React, { createContext, useState, useEffect } from 'react';

const DarkModeContext = createContext();

export const DarkModeProvider = ({ children }) => {
  const [isDarkMode, setIsDarkMode] = useState(false);

  useEffect(() => {
    try {
      const savedMode = localStorage.getItem('darkMode');
      if (savedMode !== null) {
        const parsedMode = JSON.parse(savedMode);
        if (typeof parsedMode === 'boolean') {
          setIsDarkMode(parsedMode);
        } else {
          console.error("Invalid value type for darkMode in localStorage, expected boolean.");
        }
      }
    } catch (error) {
      console.error("Error parsing JSON from localStorage:", error);
      localStorage.removeItem('darkMode'); // Clear the invalid item from localStorage
    }
  }, []);

  useEffect(() => {
    if (isDarkMode) {
      document.body.classList.add('dark-mode');
    } else {
      document.body.classList.remove('dark-mode');
    }
  }, [isDarkMode]);

  const toggleDarkMode = () => {
    setIsDarkMode(!isDarkMode);
    localStorage.setItem('darkMode', JSON.stringify(!isDarkMode));
  };

  return (
    <DarkModeContext.Provider value={{ isDarkMode, toggleDarkMode }}>
      {children}
    </DarkModeContext.Provider>
  );
};

export default DarkModeContext;
